<template>
  <div class="main">
    <router-view></router-view>

    <!-- 底部导航栏 -->
    <van-tabbar v-model="tabbarName">
      <van-tabbar-item
        :before-change="false"
        :name="item.name"
        :icon="item.icon"
        v-for="(item, index) in tabbarData"
        @click.native="goPage(item)"
        :key="index"
        >{{ item.text }}</van-tabbar-item
      >
    </van-tabbar>
  </div>
</template>
<script>
export default {
  name: "Main",

  data() {
    return {
      tabbarName: "menu",
      jump: "",
    };
  },
  created(){

  },

  computed: {
    tabbarData() {
      return this.$store.state.mainModule.tabbarData;
    },
  },

  methods: {
    //跳转页面
    
    goPage(item) {
      if (item.routeName == this.jump) {
        return;
      }
      this.jump = item.routeName;
      this.$router.push({ name: item.routeName });

    },
  },
};
</script>
<style lang="less" scoped>
</style>